import iziToast from 'izitoast';
// –î–æ–¥–∞—Ç–∫–æ–≤–∏–π —ñ–º–ø–æ—Ä—Ç —Å—Ç–∏–ª—ñ–≤
import 'izitoast/dist/css/iziToast.min.css';

// –û–ø–∏—Å–∞–Ω–∏–π —É –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü—ñ—ó
import SimpleLightbox from 'simplelightbox';
// –î–æ–¥–∞—Ç–∫–æ–≤–∏–π —ñ–º–ø–æ—Ä—Ç —Å—Ç–∏–ª—ñ–≤
import 'simplelightbox/dist/simple-lightbox.min.css';

import { fetchImages } from './js/pixabay-api.js';
import { renderGallery } from './js/render-functions.js';

const form = document.getElementById('search-form');
const arts = document.getElementById('gallery');
const loader = document.getElementById('loader');

let lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});

form.addEventListener('submit', async event => {
  event.preventDefault();

  const query = event.target.elements['search-input'].value.trim();

  if (!query) {
    iziToast.error({
      title: '‚ùå Error',
      message: 'Type the request !',
      position: 'topRight',
    });
    return;
  }

  arts.innerHTML = '';
  loader.classList.remove('hidden');

  try {
    const images = await fetchImages(query);

    if (images.length === 0) {
      iziToast.warning({
        title: '‚ùå Attention',
        message:
          'Sorry, there are no images matching your search query. Please try again!',
        position: 'topRight',
      });
      return;
    }

    renderGallery(images);
    lightbox.refresh();
  } catch (error) {
    iziToast.error({
      title: '‚ùå Error',
      message: 'Can not load the images!',
      position: 'topRight',
    });
  } finally {
    loader.classList.add('hidden');
  }
});
