export function renderGallery(images) {
  const arts = document.getElementById('gallery');

  const markup = images
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => `
        <div class="gallery-item">
            <a href="${largeImageURL}">
                <img src="${webformatURL}" alt="${tags}" loading="lazy"/>
            </a>
            <div class="info">
                <p>‚ù§Ô∏è ${likes}</p>
                <p>üëÅÔ∏è ${views}</p>
                <p>üí¨ ${comments}</p>
                <p>‚¨áÔ∏è ${downloads}</p>
            </div>
        </div>
    `
    )
    .join('');

  arts.innerHTML = markup;
}
